import React, { useEffect } from 'react'
import { graphql } from 'gatsby'
import { GatsbyImageFluidProps } from 'gatsby-image'
import Layout from './../layout/index'
import { SEO, StripeBar } from './../components'

import {
  HomeHero,
  HummingBird,
  HomeStories,
  HomeOthers,
  HomeInstagram,
} from './../containers/Home'

import Image3 from './../images/humingbird-waves.svg'
import Image4 from './../images/flame-water-drops.svg'
import Image5 from './../images/hummingbird-over-waterdrops.svg'

interface IndexPageProps {
  location: {
    pathname: string
  }
  data: {
    image1: {
      childImageSharp: GatsbyImageFluidProps
    }
    image2: {
      childImageSharp: GatsbyImageFluidProps
    }
    allInstaNode: {
      nodes: any
    }
  }
  pageContext: {
    language: string
    section1Title: string
    section1SubTitle: string
    section1Description: string
    section2Title: string
    section2Desctiption1: string
    section2Desctiption2: string
    clickHere: string
    section3: string
    seoKeyword: string
    seoDescription: string
  }
}

export default ({
  data: { image1, image2 },
  location,
  pageContext: {
    language,
    section1Title,
    section1SubTitle,
    section1Description,
    section2Title,
    section2Desctiption1,
    section2Desctiption2,
    clickHere,
    section3,
    seoDescription,
    seoKeyword,
  },
}: IndexPageProps) => {
  useEffect(() => {
    const script = document.createElement('script')
    script.src =
      'https://cdn.curator.io/published/32e06b1e-438a-491a-8b3a-500ec9a52941.js'
    script.async = true

    document.body.appendChild(script)
    return () => {}
  }, [])
  return (
    <Layout location={location}>
      <SEO
        pathname={location.pathname}
        keyword={seoKeyword}
        description={seoDescription}
        title={section1Title}
      />
      <HomeHero
        title={section1Title}
        subtitle={section1SubTitle}
        description={section1Description}
        image1={image1.childImageSharp}
        image2={image2.childImageSharp}
      />
      <StripeBar />
      <HomeInstagram images={[]} />
      <StripeBar />
      <HomeStories content={section3} image={Image5} />
      <StripeBar />
      <HummingBird
        title={section2Title}
        description1={section2Desctiption1}
        description2={section2Desctiption2}
        image1={Image3}
        image2={Image4}
      />
      <HomeOthers to='/spotlight'>{clickHere}</HomeOthers>
    </Layout>
  )
}

export const indexPageQuery = graphql`
  query IndexPageQuery {
    image1: file(relativePath: { eq: "image1.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 960, quality: 100) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    image2: file(relativePath: { eq: "image2.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 960, quality: 100) {
          ...GatsbyImageSharpFluid
        }
      }
    }

    site {
      siteMetadata {
        title
        description
      }
    }
  }
`
