import React, { useContext } from 'react'
import { Container, BackImg } from './style'
import { Button } from './../../../components'
import { LangContext } from './../../../layout'
interface IHomeHeroProps {
  title: string
  subtitle: string
  description: string
  image1: any
  image2: any
}

export default function index({
  title,
  subtitle,
  description,
  image1,
  image2,
}: IHomeHeroProps) {
  const { lang } = useContext(LangContext)

  return (
    <Container className='d-flex'>
      <BackImg className='d-none d-lg-block w-50 h-100' fluid={image1.fluid}>
        <div className='w-100 h-100'></div>
      </BackImg>
      <BackImg
        className='w-100 w-lg-50 d-flex flex-column h-100'
        fluid={image2.fluid}
      >
        <h1 className='mt-auto'>{title}</h1>
        <h2 className='mt-2'>{subtitle}</h2>
        <p className='mt-3'>{description}</p>
        <Button className='mt-4 mb-auto' type='url' to='/traditional'>
          {lang === 'fr' ? 'Savoir traditionnel' : 'Traditional Knowledge'}
        </Button>
      </BackImg>
    </Container>
  )
}
