import React, { useContext } from 'react'
import ReactHTMLParser from 'react-html-parser'
import { Remarkable } from 'remarkable'
import { Container, LeftPanel, RightPanel } from './style'
import { Button } from './../../../components'
import { LangContext } from './../../../layout'
interface IFeaturesProps {
  content: string
  image: string
}
export default ({ content, image }: IFeaturesProps) => {
  const md = new Remarkable()
  const { lang } = useContext(LangContext)

  return (
    <Container>
      <div className='d-flex container justify-content-between'>
        <LeftPanel className='w-100 w-md-50'>
          <div className='mb-5'>{ReactHTMLParser(md.render(content))}</div>
          <Button type='url' to='/stories'>
            {lang === 'fr'
              ? 'Cliquez ici pour voir ce que dautres font pour lgalit des sexes!'
              : ' Click here for Stories on what others are doing for Gender Equality!'}
          </Button>
        </LeftPanel>
        <RightPanel className='d-none d-md-flex'>
          <img src={image} className='my-auto w-100' alt='humming' />
        </RightPanel>
      </div>
    </Container>
  )
}
