import { Link } from 'gatsby'
export const Container = styled.div`
  margin: 136px auto 116px auto;
  h2 {
    color: ${props => props.theme.palette.primary};
  }
  ${down('md')} {
    margin: 116px auto 96px auto;

    h2 {
      font-size: 30px;
    }
  }
`
export const Instgram = styled.img`
  width: 68px;
  height: 68px;
`

