import React, { useContext } from "react";
import ReactHTMLParser from "react-html-parser";
import { Remarkable } from "remarkable";
import { LangContext } from "./../../../layout";
import { Container } from "./style";
import { Button, AudioPlayer } from "./../../../components";

import HummingbirdAudioFile from "./../../../audio/Hummingbird-Story.m4a";

interface IHummingBirdProps {
  title: string;
  description1: string;
  description2: string;
  image1: any;
  image2: any;
}
export default function HummingBird({
  title,
  description1,
  description2,
  image1,
  image2,
}: IHummingBirdProps) {
  const md = new Remarkable();
  const { lang } = useContext(LangContext);

  return (
    <Container>
      <div className="container">
        <div className="d-flex flex-column-reverse flex-md-row flex-wrap">
          <div className="w-100 w-md-50 my-auto">
            <h2>{title}</h2>
            <div>{ReactHTMLParser(md.render(description1))}</div>
          </div>
          <img className="w-100 w-md-50" src={image1} />
        </div>
        <div className="d-block d-md-flex flex-wrap">
          <div className="w-100 w-md-50 water-drop d-flex flex-column mt-4 mt-md-0">
            <img src={image2} alt="water drop" className="my-auto" />
          </div>
          <div className="w-100 w-md-50 mt-5">
            <div>{ReactHTMLParser(md.render(description2))}</div>

            <div className="mt-5">
              <AudioPlayer
                src={HummingbirdAudioFile}
                header={
                  lang === "fr"
                    ? `coutez l'histoire du colibri`
                    : `Listen to the Hummingbird Story`
                }
              />

              <Button
                className="mt-5"
                background="light"
                type="url"
                urlType="external"
                to="http://www.vidyaonline.net/dl/hummingbird.pdf"
              >
                {lang === "fr"
                  ? "Lire lhistoire complte crite par Michael Nicoll Yahgulanaas "
                  : "Read the full story written by Michael Nicoll Yahgulanaas"}
              </Button>
            </div>
          </div>
        </div>
      </div>
    </Container>
  );
}
